import server from "./server.js";
import froca from "./froca.js";

async function getNoteTypeItems(command) {
    const items = [
        { title: "文本", command: command, type: "text", uiIcon: "bx bx-note" },
        { title: "代码", command: command, type: "code", uiIcon: "bx bx-code" },
        { title: "保存的搜索", command: command, type: "search", uiIcon: "bx bx-file-find" },
        { title: "关系图", command: command, type: "relationMap", uiIcon: "bx bx-map-alt" },
        { title: "笔记地图", command: command, type: "noteMap", uiIcon: "bx bx-map-alt" },
        { title: "渲染笔记", command: command, type: "render", uiIcon: "bx bx-extension" },
        { title: "书", command: command, type: "book", uiIcon: "bx bx-book" },
        { title: "美人鱼图", command: command, type: "mermaid", uiIcon: "bx bx-selection" },
        { title: "画布", command: command, type: "canvas", uiIcon: "bx bx-pen" },
        { title: "网页视图", command: command, type: "webView", uiIcon: "bx bx-globe-alt" },
    ];

    const templateNoteIds = await server.get("search-templates");
    const templateNotes = await froca.getNotes(templateNoteIds);

    if (templateNotes.length > 0) {
        items.push({ title: "----" });

        for (const templateNote of templateNotes) {
            items.push({
                title: templateNote.title,
                uiIcon: templateNote.getIcon(),
                command: command,
                type: templateNote.type,
                templateNoteId: templateNote.noteId
            });
        }
    }

    return items;
}

export default {
    getNoteTypeItems
}
